@import url(https://use.fontawesome.com/releases/v5.3.1/css/all.css);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Joke {
  margin-bottom: 0.75em;
}

.Joke-votearea {
  display: inline-block;
  vertical-align: top;
}

.Joke-votearea > * {
  margin-right: 1em;

}
.Joke-text {
  margin-left: 1em;
  display: inline-block;
  width: 500px;
}
.loading {
	width: 4em;
	margin: 200px auto 0 auto;
	color: tomato;
}

.JokeList-getmore {
	margin: 1em 0;
	font-size: 150%;
	border-radius: 0.5em;
	background-color: tomato;
	color: white;
}

.JokeList {
	list-style-type: none;
	margin: 0 auto;
	padding: 2rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border-radius: 2rem;
	height: auto;
	width: 40rem;
	background-color: rgba(246, 244, 244, 0.928);
	box-shadow: 5px 10px 18px #888888;
}

.Jokes {
	padding-top: 5rem;
}

.Title {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

